<template>
    <div>
        <table class="board-table">
            <thead>
            <tr>
                <th></th>
                <th></th>
                <th></th>
                <th></th>
                <th> </th>
            </tr>
            </thead>
            <tbody>
            <tr v-for="(board, index) in boards" :key="board.title" @click="goToBoardDetails(board.boardId)">
                <td>{{ board.boardId }}</td>
                <td>{{ board.title }}</td>
                <td>{{ board.username }}</td>
                <td>{{ board.viewCnt }}</td>
                <td>{{ board.likesCnt }}</td>
            </tr>
            </tbody>
        </table>
        <v-btn @click="$router.push('/board-create')"></v-btn>
        <!-- Pagination   totalPages    -->
        <Pagination
            :totalPages="totalPages"
            @page-changed="handlePageChange"
        />
    </div>
</template>

<script>
import { readBoards } from "@/api";
import Pagination from "@/components/Pagination.vue";
export default {
    name: "BoardList",
    components: {
        Pagination,
    },
    data() {
        return {
            boards: [], //     
            totalPages: 10, //   ( 10 )
        };
    },
    methods: {
        async fetchBoards(page) {
            try {
                const response = await readBoards(page);
                const pageDto = response.data;
                this.totalPages = pageDto.lastPage; // totalPages 
                this.boards = pageDto.content; //   
                console.log(pageDto);
            } catch (error) {
                console.error("Error fetching boards:", error);
            }
        },
        async handlePageChange(page) {
            console.log(` : ${page}`);
            this.currentPage = page;
            await this.fetchBoards(page); //      
        },
        goToBoardDetails(boardId) {
            this.$store.dispatch('updateBoardId', boardId);
            this.$router.push(`/board-details`);
        }
    },
    created() {
        this.fetchBoards(this.currentPage); //       
    },
};
</script>

<style scoped>
.board-table {
    width: 100%;
    border-collapse: collapse;
}

.board-table th,
.board-table td {
    border: 1px solid #ddd;
    padding: 8px;
}

.board-table th {
    background-color: #f2f2f2;
}
</style>